// @ts-nocheck
/**
 * ACU  - Vue 
 *
 *  iframe 
 * Vue  document
 *
 *  API  Vue Composables
 *  App.vue  Tab 
 */

import { polyfill } from 'mobile-drag-drop';
import 'mobile-drag-drop/default.css';
import { scrollBehaviourDragImageTranslateOverride } from 'mobile-drag-drop/scroll-behaviour';
import { createPinia } from 'pinia';
import { createApp, type App as VueApp } from 'vue';
import App from './App.vue';
import { getACUConfigManager } from './composables/useACUConfigManager';
import { cleanupSendIntercept, setupFocusTracking, setupSendIntercept } from './composables/useHiddenPrompt';
import { useDivinationStore } from './stores/useDivinationStore';
import { useUIStore } from './stores/useUIStore';
import { getCore } from './utils/index';
import { VERSION } from './version';

//  polyfill
//  HTML5  API 
polyfill({
  //  300ms 
  holdToDrag: 300,
  // 
  dragImageTranslateOverride: scrollBehaviourDragImageTranslateOverride,
});

// jQuery  -  jQuery 
(window as any).$ = (window.parent as any).$;

const SCRIPT_ID = 'acu-parent-container';
const MAX_DATA_RETRIES = 10;

// 
const LOCAL_KEYS = {
  WIN_CONFIG: 'acu_win_config',
  ACTIVE_TAB: 'acu_active_tab',
  UI_COLLAPSE: 'acu_ui_collapse_state',
} as const;

/**
 *  6.4.1
 *
 *  Dashboard 
 * 
 * 1.  Tab
 * 2. 
 *
 *  Vue  Pinia store 
 */
function checkAndResetCenteredMode(): void {
  try {
    const winConfigRaw = localStorage.getItem(LOCAL_KEYS.WIN_CONFIG);

    // 1
    if (!winConfigRaw) {
      // 
      const defaultConfig = { width: 400, left: '50%', bottom: '50%', isCentered: true };
      localStorage.setItem(LOCAL_KEYS.WIN_CONFIG, JSON.stringify(defaultConfig));
      //  activeTab
      localStorage.removeItem(LOCAL_KEYS.ACTIVE_TAB);
      // 
      localStorage.setItem(LOCAL_KEYS.UI_COLLAPSE, 'false');
      console.info('[ACU]  activeTab');
      return;
    }

    // 2
    const winConfig = JSON.parse(winConfigRaw);
    if (winConfig?.isCentered === true) {
      //  activeTab
      localStorage.removeItem(LOCAL_KEYS.ACTIVE_TAB);
      // 
      localStorage.setItem(LOCAL_KEYS.UI_COLLAPSE, 'false');
      console.info('[ACU]  activeTab');
    }
  } catch (e) {
    // 
    const defaultConfig = { width: 400, left: '50%', bottom: '50%', isCentered: true };
    localStorage.setItem(LOCAL_KEYS.WIN_CONFIG, JSON.stringify(defaultConfig));
    localStorage.removeItem(LOCAL_KEYS.ACTIVE_TAB);
    localStorage.setItem(LOCAL_KEYS.UI_COLLAPSE, 'false');
    console.warn('[ACU]  win_config :', e);
  }
}

// Vue  ()
let vueApp: VueApp | null = null;
let mountContainer: HTMLElement | null = null;

//  ID ()
let dbCheckTimerId: ReturnType<typeof setTimeout> | null = null;

//  ()
let isShuttingDown = false;

//  API 
let isDatabaseReady = false;

/**
 *  Vue 
 */
function initVueApp() {
  const parentDoc = window.parent.document;

  // 
  const existingRoot = parentDoc.getElementById(SCRIPT_ID);
  if (existingRoot) {
    console.warn('[ACU] Vue ');
    return;
  }

  //  - ID  'acu-parent-container'
  mountContainer = parentDoc.createElement('div');
  mountContainer.id = SCRIPT_ID;
  parentDoc.body.appendChild(mountContainer);

  //  Pinia 
  const pinia = createPinia();

  //  Vue 
  vueApp = createApp(App);
  vueApp.use(pinia);

  // 
  //  API  App.vue  composables
  //  Vue  Tab 
  vueApp.mount(mountContainer);

  console.info(`[ACU] Vue  (${SCRIPT_ID})`);
}

/**
 *  API  UI
 *
 *  Promise API  resolve
 */
function waitForDatabaseApi(): Promise<void> {
  return new Promise(resolve => {
    let dataCheckRetries = 0;

    const checkApi = () => {
      // 
      if (isShuttingDown) {
        console.info('[ACU]  API ');
        return;
      }

      const { $, getDB } = getCore();
      const api = getDB();

      //  API  jQuery 
      if (api && typeof api.exportTableAsJson === 'function' && $) {
        // 
        const data = api.exportTableAsJson();
        const hasData = data && Object.keys(data).length > 0;

        // 
        if (!hasData && dataCheckRetries < MAX_DATA_RETRIES) {
          dataCheckRetries++;
          console.info(`[ACU]  API ... (${dataCheckRetries}/${MAX_DATA_RETRIES})`);
          dbCheckTimerId = setTimeout(checkApi, 1000);
          return;
        }

        //  ID
        dbCheckTimerId = null;
        isDatabaseReady = true;
        resolve();
      } else {
        // API 
        dbCheckTimerId = setTimeout(checkApi, 1000);
      }
    };

    checkApi();
  });
}

/**
 *  ACU
 *
 * 
 * 1. extensionSettings 
 * 2.  API  UI
 * 3.  Vue 
 */
function initACU() {
  //  Vue 
  //  Pinia store  activeTab 
  checkAndResetCenteredMode();

  // 1. extensionSettings 
  const configManager = getACUConfigManager();
  configManager.loadConfig();
  console.info('[ACU] ');

  // 2.  API  UI
  waitForDatabaseApi().then(() => {
    console.info('[ACU]  API ');
  });

  // 3.  Vue 
  initVueApp();
}

/**
 *  Vue 
 *
 *  API  Vue Composables
 *  Vue 
 */
function cleanup() {
  console.info('[ACU] ...');

  // 
  isShuttingDown = true;

  // 
  if (dbCheckTimerId !== null) {
    clearTimeout(dbCheckTimerId);
    dbCheckTimerId = null;
    console.info('[ACU] ');
  }

  //  Vue 
  // composables 
  if (vueApp) {
    vueApp.unmount();
    vueApp = null;
    console.info('[ACU] Vue ');
  }

  // 
  if (mountContainer) {
    mountContainer.remove();
    mountContainer = null;
    console.info('[ACU] ');
  }

  console.info('[ACU] ');
}

// ============================================================
// 
// ============================================================

$(() => {
  console.info(`[ACU] v${VERSION} `);

  // 
  replaceScriptButtons([{ name: ' ', visible: true }]);

  // 
  eventOn(getButtonEvent(' '), () => {
    //  Vue 
    if (!vueApp) {
      console.warn('[ACU] Vue ');
      return;
    }

    //  store 
    const uiStore = useUIStore();
    const currentPrompt = (window as any).__acu_hidden_prompt || '';
    uiStore.openPromptEditorDialog(currentPrompt);
  });

  //  AI 
  //  AI 
  //  'generation_started' 'js_generation_started' generate 
  eventOn('generation_started', () => {
    if (!vueApp) return;

    const divinationStore = useDivinationStore();
    // 
    if (divinationStore.config.enableWordPool && divinationStore.config.autoSync) {
      console.info('[ACU]  AI ...');
      divinationStore.syncFromACU();
    }
  });

  //  DOM 
  setTimeout(() => {
    setupSendIntercept();
    setupFocusTracking(); // 
  }, 500);

  //  ACU +  +  Vue
  errorCatched(initACU)();

  //  ( pagehide  unload)
  $(window).on('pagehide', () => {
    console.info('[ACU] ');
    //  DOM 
    cleanupSendIntercept();
    cleanup();
  });
});
